import React from 'react';
import AppBar from 'material-ui/AppBar';
import { withTheme } from 'material-ui/styles';
import Toolbar from 'material-ui/Toolbar';
import IconButton from 'material-ui/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import CloseIcon from '@material-ui/icons/Close';
import InfoIcon from '@material-ui/icons/Info';
import SwipeableDrawer from 'material-ui/SwipeableDrawer';
import Divider from 'material-ui/Divider';
import List, { ListItem, ListItemIcon, ListItemText } from 'material-ui/List';
import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog';
import Button from 'material-ui/Button';
import './AppBar.css';


class NewtAppBar extends React.Component {
  state = {
    drawerOpen: false,
    aboutDialogOpen: false,
  };

  toggleDrawer = (open) => {
    this.setState({ drawerOpen: open });
  };

  handleAboutDialogClose = () => {
    this.setState({
      aboutDialogOpen: false
    })
  };

  render = () => {

    const AboutDialog = (
      <Dialog
        open={this.state.aboutDialogOpen}
        onClose={this.handleAboutDialogClose}
      >
        <DialogTitle>{"Now you can see me"}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            <div>You got me<br/>Thank you for visiting my site.</div>
          </DialogContentText>
          <DialogActions>
            <Button onClick={() => this.setState({ aboutDialogOpen: false })} color='primary' autoFocus>
              Close
            </Button>
          </DialogActions>
        </DialogContent>
      </Dialog>
    );

    const drawerListItems = (
      <div>
        <ListItem button>
          <ListItemIcon>
            <InboxIcon></InboxIcon>
          </ListItemIcon>
          <ListItemText primary='InBox'></ListItemText>
        </ListItem>
        <ListItem button>
          <ListItemIcon>
            <InfoIcon/>
          </ListItemIcon>
          <ListItemText primary='Account'></ListItemText>
        </ListItem>
      </div>
    );

    const drawerInfoListItems = (
      <div>
        <ListItem button>
          <ListItemIcon>
            <InboxIcon></InboxIcon>
          </ListItemIcon>
          <ListItemText primary='InBox'></ListItemText>
        </ListItem>
        <ListItem button onClick={() => this.setState({ aboutDialogOpen: true })}>
          <ListItemIcon>
            <InfoIcon></InfoIcon>
          </ListItemIcon>
          <ListItemText primary='About'></ListItemText>
        </ListItem>
      </div>
    );

    return (
      <div className='AppBar-root' color="primary">
        {AboutDialog}
        <AppBar position='static' color="primary">
          <Toolbar>
            <IconButton className='AppBar-menu-button'>
              { this.state.drawerOpen ? <CloseIcon onClick={() => this.toggleDrawer(false)}/> : <MenuIcon onClick={() => this.toggleDrawer(true) }/> }
            </IconButton>
          </Toolbar>
        </AppBar>

        <SwipeableDrawer
          open={this.state.drawerOpen}
          onOpen={ ()=> {} }
          onClose={ () => this.toggleDrawer(false) }
        >
          <div
            tabIndex={0}
            role="button"
            onClick={ () => this.toggleDrawer(false) }
            onKeyDown={ () => this.toggleDrawer(false) }
          >
            <List>
              {drawerListItems}
            </List>
            <Divider/>
            <List>
              {drawerInfoListItems}
            </List>
          </div>
        </SwipeableDrawer>
      </div>
    );
  }
}

export default withTheme()(NewtAppBar);


